import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { ButtonBase, Stack, SxProps } from "@rp/react/web/mui/core"
import { ChevronLeft, ChevronRight } from "@rp/react/web/mui/icons"

type Props = {
  direction: "left" | "right"
  onClick: () => void
  onMouseDown?: (event: React.MouseEvent) => void
  bgColor: string
  sx?: SxProps
}
const ScrollCarouselArrow = ({ direction, onClick, sx, onMouseDown }: Props) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Stack
      justifyContent="center"
      component="span"
      sx={{
        position: "absolute",
        top: 0,
        left: direction === "left" ? 0 : undefined,
        right: direction === "right" ? 0 : undefined,
        pr: direction === "left" ? 8 : 0,
        pl: direction === "right" ? 4 : 0,
        pt: 0.5,
        height: "100%",
        background: `inherit`,
        zIndex: 1,
        pointerEvents: "none",
        ...sx,
      }}
    >
      <ButtonBase
        disableRipple
        sx={{ pointerEvents: "all" }}
        onClick={onClick}
        onMouseDown={onMouseDown}
      >
        {!isMobile && (direction === "left" ? <ChevronLeft /> : <ChevronRight />)}
      </ButtonBase>
    </Stack>
  )
}
export { ScrollCarouselArrow }
